








































































































































.user-layout-login {
  label {
    font-size: 14px;
  }

  .forget-password {
    font-size: 14px;
  }

  .register {
    float: right;
  }

  button.login-button {
    padding: 0 15px;
    font-size: 16px;
    height: 40px;
    width: 100%;
  }

  .user-login-other {
    text-align: left;
    margin-top: 24px;
    line-height: 22px;

    .forget-password {
      float: left;
    }

    .register {
      float: right;
    }
  }
}

@primary-color: #52C41A;@link-color: #52C41A;@border-radius-base: 2px;